* {
  margin:0;
  padding:0;
}
.container-fluid{
  background: linear-gradient(to right bottom, #e1b382, #c89666);
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.app-wrapper{
  background-color:#12343b ;
  min-width: 450px;
  min-height: 650px;
  padding:30px ;
  box-sizing:border-box ;
  border-radius:10px ;
  box-shadow: 3px 6px 40px #000;
  margin-bottom: 10px;
}
.header h1{
  color: #fff;
  text-align: center;
  margin: 30px 0;
}
.task-input {
  outline:none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: #ccc;
  background-color: #4e4a4a;
  border: 1px solid #c89666;
  border-radius: 10px;
}
.button-add{
  width: wrap;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #f1af71;
  cursor:pointer;
}
.list-item{
  display: flex;
  margin: 20px 0px;
  border: 2px solid #ccc;
  border-radius: 20px;
  padding: 10px;
  max-height: 50px;
}
.task-button{
  justify-content: center;
  align-items: center;
}
.button-complete,
.button-delete,
.button-edit{
  border:none;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50%;

}
.button-complete{
  color: #ff6c6c;
  margin-right: 10px;
}
.button-edit{
  color: #e2d029;
  margin-right: 10px;
}
.button-delete{
  color: lightseagreen;
}
.complete{
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #ff6c6c;
  opacity: 0.6;
}
.list{
  width: 220px;
  background-color: #12343b;
  border: none;
  color: #ccc;
  font-size: 20px;
  padding-left: 10px;
  padding-right: 15px;
}
input:focus,button:focus{
  outline: none;
}
